import React from 'react'
import { Card } from 'antd';
import {DeleteOutlined, EditOutlined} from '@ant-design/icons'

function Cards({product, handleRemove, slug}) {
    const { Meta } = Card;
    const {title, description, images} = product;
    // console.log(images)
    return (
        <div>
             <Card
    hoverable
    className="p-1"
    style={{height:'100px',objectFit:"cover" , width:"300px"}}
    cover={<img alt="example" src={images && images.length ? images[0].url : "data:image/png;base64,"}  />}

    actions={[
      
      <EditOutlined className="text-warning" key="edit" onClick={() => window.alert("This Feature is not created")} />,
      <DeleteOutlined onClick={() => handleRemove(slug)} className="text-danger" key="delete" />,
    ]}
  >
    <Meta title={title} description={`${description && description.substring(0, 40)}`} />
  </Card>
        </div>
    )
}

export default Cards
